<template>
  <div class="-m-home">
    <h2 class="time">{{ time }}</h2>
    <span class="clock-in" @click="clockIn">{{ btnText }}</span>
  </div>
</template>

<style>
  .-m-home .time{display: block;text-align: center;margin-top: 20%;}
  .-m-home .clock-in{width: 80%;height: 45px;line-height: 45px;display: block;text-align:center;border-radius:4px;color:#ffffff;background-color: #23c454;margin: 20% auto 0 auto;}
</style>

<script>

  import Toast from 'mint-ui/lib/toast';
  import MessageBox from 'mint-ui/lib/message-box';

  export default {
    name: 'home',

    data () {
      return {
        time: new Date().toLocaleTimeString(),
        btnText: "睡觉喽"
      }
    },

    methods: {
      clockIn: function () {
        let _now = new Date();
        const _day = _now.toLocaleDateString();
        const _week = "星期" + "日一二三四五六".split("")[_now.getDay()];
        let _time = _now.toLocaleString("chinese",{hour12:false});
        _time = _time.substring(_time.indexOf(" ") + 1);
        const _the_record = {day: _day, week: _week, time: _time};
        if (localStorage.getItem("CLOCK_IN_RECORD")) {
          let _clockIn_record = JSON.parse(localStorage.getItem("CLOCK_IN_RECORD"));
          if (CGI.isClockIn()) {
            MessageBox.confirm('今日已打卡，是否确认更新?').then(action => {
              if (action) {
                _clockIn_record.filter((k, index) => {if (k.day === _day) {
                  _clockIn_record[index].time = _time;
                  localStorage.setItem("CLOCK_IN_RECORD", JSON.stringify(_clockIn_record));
                  Toast({
                    message: '更新成功',
                    position: 'bottom'
                  });
                }});
              }
            });
          } else {
            _clockIn_record.push(_the_record);
            localStorage.setItem("CLOCK_IN_RECORD", JSON.stringify(_clockIn_record));
            Toast({
              message: '打卡成功',
              position: 'bottom'
            });
          }
        } else {
          localStorage.setItem("CLOCK_IN_RECORD", JSON.stringify([_the_record]));
          Toast({
            message: '打卡成功',
            position: 'bottom'
          });
        }
        this.btnText = "更新打卡";
      }

    },

    created() {
      setInterval(function () {
        let _now = new Date().toLocaleTimeString();
        this.time = _now;
      }.bind(this), 1000);

      if (CGI.isClockIn()) {
          this.btnText = "更新打卡";
      }
    },

    component: {Toast, MessageBox}
  };
</script>




