/**
 * http 请求
 *
 * Created by seaton on 2017/3/1 0001.
 */

import axios from 'axios';

// axios 全局参数设置
axios.defaults.baseURL = '';
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';

/**
 * 返回值说明：
 *
 *  code : 0[成功]，其他失败
 *  msg ：请求成功/请求失败
 *  data : response中的data属性
 */

const Http = {

  _get : function (url) {
    return axios.get(url).then((data) => {
      if (data.status === 200 && data.statusText === "OK") {
        return {code : 0, msg : "请求成功", data : data.data};
      }
    }).catch((response) => {
      if (response instanceof Error) {
        return {code : 1, msg : "请求失败", data : {}};
      }
    });
  },

  _post : function (url, param) {
    return axios.post(url, param).then((data) => {
      if (data.status === 200 && data.statusText === "OK") {
        return {code : 0, msg : "请求成功", data : data.data};
      }
    }).catch((response) => {
      if (response instanceof Error) {
        return {code : 1, msg : "请求失败", data : {}};
      }
    });
  }

};

export default Http;




