import Vue from 'vue';
import Router from 'vue-router';
import 'mint-ui/lib/style.css';
import Home from '../containers/Home';

require('../assets/styles/style.css');
require('../common/CGI');

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    }
  ]
});



