import Http from './Http';

window.CGI = {

  isClockIn: function () { // 今日是否已打卡
    let _now = new Date();
    const _day = _now.toLocaleDateString();
    const _clockIn_record = JSON.parse(localStorage.getItem("CLOCK_IN_RECORD")) || [];
    const _len = _clockIn_record.filter((k) => {if (k.day === _day){return k;}});
    if (_len.length > 0) {
      return true;
    }
    return false;
  }
};




